import React from 'react';
import styled from 'styled-components';

import Header from '../../components/common/Header';
import Footer from '../../components/common/Footer';

import { PROJECTS } from '../../constants';

const Container = styled.div`
  background: #212529;
  color: #87d8d6;

  @media (max-width: 759px) {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 15%;
  }

  @media (min-width: 760px) {
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-columns: 20% 1fr 20%;
    grid-template-rows: 10% auto 10%;

    & > div:nth-of-type(1) {
      grid-column: 1 / 4;
      grid-row: 1 / 2;
    }

    & > div:nth-of-type(2) {
      grid-column: 1 / 4;
      grid-row: 3 / 4;
    }
  }
`;

const Contents = styled.ul`
  list-style: none;

  @media (max-width: 759px) {
    padding: 0;
    margin: 0;
  }

  @media (min-width: 760px) {
    grid-column: 2 / 3;
    display: grid;
    padding: 0 2rem;
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 1rem;
    column-gap: 1rem;
  }
`;

const Company = styled.div`
  position: absolute;
  bottom: 0;
  font-size: 1rem;
  color: white;
  width: 100%;
  background: dodgerblue;
  opacity: 0;
`;

const Work = styled.li`
  border: 1px solid #87d8d6;
  /* background-image: url(${(props) => props.url});
  background-repeat: no-repeat; */
  /* background-size: 100% 100%; */
  /* background-position: center; */
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: 1.5rem;
  text-align: center;
  position: relative;

  &:hover  {
    ${Company} {
      background: #87d8d6;
      color: #212529;
      opacity: 1;
    }
  }

  @media (max-width: 759px) {
    width: 300px;
    height: 150px;
    margin: 2rem 0;
  }

  @media (min-width: 760px) {
    :nth-child(1) {
      grid-column: 1 / span 2;
      grid-row: 1 / span 2;
    }

    :nth-child(4) {
      grid-column: 1 / 2;
      grid-row: 3 /5;
    }

    :nth-child(5),
    :nth-child(6) {
      grid-column: 2 / 4;
    }

    :nth-child(6) {
      grid-column: 2 / 4;
    }

    :nth-child(7) {
      grid-column: 1 / 4;
    }
  }
`;

const Works = ({ history }) => {
  return (
    <>
      <Container className="contents">
        <Header className="header" />
        <Contents>
          {PROJECTS.map((item, i) => {
            return (
              <Work
                key={i}
                // url={item.src}
                title={item.title}
                company={item.company}
                onClick={() => history.push(item.goToPage)}
              >
                {item.title}
                <Company>{item.company}</Company>
              </Work>
            );
          })}
        </Contents>
        <Footer className="footer" />
      </Container>
    </>
  );
};

export default Works;
